body {
    font-family: sans-serif;
    margin: 0 auto;
    padding: 25px;
    /* max-width: 400px; */
    /* min-width: 400px; */
    background-color: #dfe6e9;
    text-align: center;
  }
  
  .heading {
    text-align: center;
    font-size: 2em;
    color: #2d3436;
    margin: 20px 0 30px;
  }
  
  .card {
    /* height: 400px; */
    width: 400px;
    border-radius: 25px;
    position: relative;
    padding: 25px 15px;
    background-color: #FFEBEB;
    margin: 50px 15px;
    height: 200px;
    box-shadow: 0 2px 5px #ccc;
    text-align: left;
    display: inline-block;
  }
  
  .top {
    border-radius: 25px 25px 0 0;
    height: 125px;
    width: 100%;
    background-color: #FF55BB;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
  }
  
  .name {
    font-size: 2em;
    /* color: #2d3436; */
    color: white;
    display: flex;
    flex: 1;
    margin: 10% 20px 0;
  }
  
  .card img {
    margin: 30px 20px 0 0;
  }
  
  .circle-img {
    border-radius: 50%;
    border: 7px solid #fff;
    width: 120px;
    height: 120px;
  }
  
  .bottom {
    margin-top: 120px;
  }
  
  .info {
    margin: 20px 0;
    color: #1a7e76;
  }
  